<template>
<v-container>
  <!-- Tab bar -->
  <v-row class="ma-0 pa-0">
    <v-col class="ma-0 pa-0">
      <template>
        <v-tabs v-model="tab" id="tabBar">
          <v-tabs-slider></v-tabs-slider>
          <v-tab v-for="item in tabItems" :key="item.tab" @click="tabChange(item.tab)">
            {{ item.tab }}
          </v-tab>
        </v-tabs>
      </template>
    </v-col>
  </v-row>
  <!-- Tab bar -->
  <!-- Data Table -->
  <v-row class="ma-0 pa-0">
    <v-col class="ma-0 pa-0">
      <v-data-table :headers="headersLocation" :items="locations" item-key="id" class="elevation-1" disable-pagination hide-default-footer multi-sort fixed-header height="100%" id="dataTableContent1">
        <template v-slot:item="props">
          <tr style="cursor: pointer;" @click="OnLocationSelect(props.item)">
            <td align="center">
              {{ props.item.name }}
            </td>
            <td align="center">
              {{ props.item.phoneNumber }}
            </td>
            <td align="center">
              {{ props.item.mobileNumber }}
            </td>
            <td align="start">
              {{ props.item.address }} {{ props.item.city }} {{ props.item.state }}
            </td>
            <td align="center">
              {{ props.item.zipCode }}
            </td>
            <td align="center">
              <v-img v-if="props.item.isDefault" alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/checkOn.png" width="25px" />
              <v-img v-else alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/checkOff.png" width="25px" />
            </td>
          </tr>
        </template>
      </v-data-table>
    </v-col>
  </v-row>
  <v-row style="margin-bottom: 30px;margin-right: 3px;">
    <v-col class="ma-0 pa-0" align="right">
      <v-tooltip top color="#9691BD">
        <template v-slot:activator="{ on, attrs }">
          <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px" contain src="@/images/add.png" width="43px" @click="OnLocationAdd('')"/>
        </template>
        <span>New</span>
      </v-tooltip>  
    </v-col>
  </v-row>
  <!-- Data Table -->
  <!-- Data Table -->
  <v-row class="ma-0 pa-0" style="padding-top: 30px;">
    <v-col class="ma-0 pa-0">
      <v-data-table :headers="headersCarrier" :items="carriers" item-key="id" class="elevation-1" disable-pagination hide-default-footer multi-sort fixed-header height="100%" id="dataTableContent2">
        <template v-slot:item="props">
          <tr>
            <td align="center">
              <v-img style="margin:8px" alt="" class="shrink mt-1 hidden-sm-and-down" contain :src="getRateImgUrl(props.item.name)" width="35px" />
              {{ getCarrierName(props.item.name) }}
            </td>
            <td align="center">
              {{ props.item.service }}
            </td>
            <td align="center">
              {{ props.item.origin }}
            </td>
            <td align="center">
              {{ props.item.destination }}
            </td>
            <td align="center">
              {{ props.item.weightLimit }} {{ props.item.weightLimitUnit }}
            </td>
            <td align="start">
              {{ props.item.memo }}
            </td>
            <td align="center">
              <v-img v-if="props.item.active" alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/checkOn.png" width="25px" />
              <v-img v-else alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/checkOff.png" width="25px" />
            </td>
            <td align="center">
              <v-img alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/rate.png" width="25px" style="cursor:pointer;top:2px" @click="downloadRate(props.item)" />
            </td>
          </tr>
        </template>
      </v-data-table>
    </v-col>
  </v-row>
  <!-- Data Table -->  
    <!-- 로딩바 -->
    <loadingBarView :setLoadingBar="this.setLoadingBar"></loadingBarView>
    <!-- 로딩바 -->
    <!-- 등록 다이얼로그 -->
    <v-dialog v-model="locationCreateDialogShow" persistent max-width="500px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
      </template>
      <v-card>
        <v-card-text>
          <v-card-title class="ma-0 pa-0">
              <span class="text-h5" style="margin-top: 20px;margin-bottom: 20px;">Warehouse Registration</span>
            </v-card-title>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Shipping From (*)" placeholder="Shipping From" outlined v-model="createLocation.name" ref="shippingFrom"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Phone Number (*)" placeholder="Only number. ex) 01012345678" outlined v-model="createLocation.phoneNumber" ref="phoneNumber" oninput="javascript: this.value = this.value.replace(/\D/g, '' );"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Mobile Number (*)" placeholder="Only number. ex) 01012345678" outlined v-model="createLocation.mobileNumber" ref="mobileNumber" oninput="javascript: this.value = this.value.replace(/\D/g, '' );"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Address (*)" placeholder="Address" outlined v-model="createLocation.address" ref="address"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="City (*)" placeholder="City" outlined v-model="createLocation.city" ref="city"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="State (*)" placeholder="State" outlined v-model="createLocation.state" ref="state"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="PostCode (*)" placeholder="PostCode" outlined v-model="createLocation.zipCode" ref="postCode"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-switch v-model="createLocation.isDefault" inset :label="`Default`" hide-details></v-switch>
              </v-col>
            </v-row>
        </v-card-text>            
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" outlined @click="OnLocationCancel"> Cancel </v-btn>
          <v-btn color="primary" @click="OnLocationCreateOk"> Ok </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- 등록 다이얼로그 -->

    <!-- 수정 다이얼로그 -->
    <v-dialog v-model="locationModifyDialogShow" persistent max-width="500px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
      </template>
      <v-card>
        <v-card-text>
          <v-card-title class="ma-0 pa-0">
              <span class="text-h5" style="margin-top: 20px;margin-bottom: 20px;">Warehouse Registration</span>
            </v-card-title>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Shipping From (*)" placeholder="Shipping From" outlined v-model="modifyLocation.name" ref="shippingFrom"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Phone Number (*)" placeholder="Only number. ex) 01012345678" outlined v-model="modifyLocation.phoneNumber" ref="phoneNumber" oninput="javascript: this.value = this.value.replace(/\D/g, '' );"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Mobile Number (*)" placeholder="Only number. ex) 01012345678" outlined v-model="modifyLocation.mobileNumber" ref="mobileNumber" oninput="javascript: this.value = this.value.replace(/\D/g, '' );"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="Address (*)" placeholder="Address" outlined v-model="modifyLocation.address" ref="address"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="City (*)" placeholder="City" outlined v-model="modifyLocation.city" ref="city"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="State (*)" placeholder="State" outlined v-model="modifyLocation.state" ref="state"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field dense hide-details label="PostCode (*)" placeholder="PostCode" outlined v-model="modifyLocation.zipCode" ref="postCode"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-switch v-model="modifyLocation.isDefault" inset :label="`Default`" hide-details></v-switch>
              </v-col>
            </v-row>            
        </v-card-text>            
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" outlined @click="OnLocationCancel"> Cancel </v-btn>
          <v-btn color="error" @click="OnLocationDelete"> Delete </v-btn>
          <v-btn color="primary" @click="OnLocationModifyOk"> Ok </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- 수정 다이얼로그 -->
  </v-container>
</template>
<style scoped>
</style>
<script>
import common from '@/common'
import axios from "axios";
import $ from "jquery";
import VueCookies from "vue-cookies";
import dateUtil from "@/common/DateUtil"
import loadingBarView from "@/components/LoadingBarView.vue"

export default {
    data() {
        return {
            locationCreateDialogShow: false,
            locationModifyDialogShow: false,
            setLoadingBar : { loadingBarDialogShow : false },

            createLocation: {},
            modifyLocation: {},
            locations: [],

            carriers: [],

            tab: 2,
            tabItems: [
                { tab: 'My Details', content: 'Tab 1 Content' },
                { tab: 'Payment', content: 'Tab 2 Content' },
                { tab: 'Shipping', content: 'Tab 3 Content' },
                { tab: 'Packaging', content: 'Tab 4 Content' },
            ],
            headersLocation: [
                { text: "SHIPPING FROM", value: "data1", width: "250", align: "center" },
                { text: "PHONE NUMBER", value: "data2", width: "250", align: "center" },
                { text: "MOBILE NUMBER", value: "data3", width: "250", align: "center" },
                { text: "ADDRESS", value: "data4", width: "600" },
                { text: "POSTCODE", value: "data5", width: "150", align: "center" },
                { text: "DEFAULT", value: "data6", width: "250", align: "center" },
            ],
            headersCarrier: [
                { text: "CARRIER", value: "data1", width: "200", align: "center" },
                { text: "SERVICE", value: "data2", width: "200", align: "center" },
                { text: "ORIGIN", value: "data3", width: "200", align: "center" },
                { text: "DESTINATION", value: "data4", width: "200", align: "center" },
                { text: "WEIGHT LIMITS", value: "data5", width: "200", align: "center" },
                { text: "CONDITIONS", value: "data6", width: "" },
                { text: "ACTIVE", value: "data7", width: "150", align: "center" },
                { text: "RATE", value: "data8", width: "150", align: "center" },
            ],
        };
    },
    components: {
        loadingBarView
    },
    filters: {       
    },
    computed: {
    },
    methods: {
        getRateImgUrl(value) {
          return common.getRateImgUrl(value)
        },
        getCarrierName(value) {
          return common.getCarrierName(value)
        },
        OnLocationAdd() {
            this.createLocation = {};
            this.locationCreateDialogShow = true;
        },
        OnLocationSelect(location) {
            this.modifyLocation = {...location};
            this.locationModifyDialogShow = true;
        },
        tabChange(value) {
            if(value == "My Details") { this.$router.push("/seller-master"); }
            if(value == "Payment") { this.$router.push("/seller-master-payment"); }
            //if(value == "Shipping") { this.$router.push("/seller-master-shipping"); }
            if(value == "Packaging") {this.$router.push("/seller-master-packaging"); }
        },    
        onCountrySelect({ iso2 }) {
            alert(iso2);
        },
        makeRateFileName(carrier) {
            let fileName = carrier + "_RATE" + "_" + VueCookies.get("companyName") + "_" + dateUtil.nowYYYYmmdd() + ".xlsx";
            return fileName;
        },
        getLocations() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }      

            this.setLoadingBar.loadingBarDialogShow = true;

            axios.get(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/locations')
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                this.locations = response.data;
                this.setLoadingBar.loadingBarDialogShow = false;
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.setLoadingBar.loadingBarDialogShow = false;
            })
        },
        OnLocationCancel() {
            this.locationCreateDialogShow = false;
            this.locationModifyDialogShow = false;

            this.createLocation = {};
            this.modifyLocation = {};
        },
        OnLocationCreateOk() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }

            if (!this.createLocation.name) {
              this.$alert(common.messages.shippingFrom, "", "info").then(() => {
                this.$refs.shippingFrom.focus();
              });
              return false;
            }
            if (!this.createLocation.phoneNumber) {
              this.$alert(common.messages.phoneNo, "", "info").then(() => {
                this.$refs.phoneNumber.focus();
              });
              return false;
            }
            if (!this.createLocation.mobileNumber) {
              this.$alert(common.messages.mobileNo, "", "info").then(() => {
                this.$refs.mobileNumber.focus();
              });
              return false;
            }
            if (!this.createLocation.address) {
              this.$alert(common.messages.address1, "", "info").then(() => {
                this.$refs.address.focus();
              });
              return false;
            }
            if (!this.createLocation.city) {
              this.$alert(common.messages.addressCity, "", "info").then(() => {
                this.$refs.city.focus();
              });
              return false;
            }
            if (!this.createLocation.state) {
              this.$alert(common.messages.addressState, "", "info").then(() => {
                this.$refs.state.focus();
              });
              return false;
            }
            if (!this.createLocation.zipCode) {
              this.$alert(common.messages.zipCode, "", "info").then(() => {
                this.$refs.postCode.focus();
              });
              return false;
            }

            this.createLocation.sellerId = this.$session.get('loginId');
            this.createLocation.isDefault = (this.createLocation.isDefault == null) ? false : this.createLocation.isDefault;
            this.setLoadingBar.loadingBarDialogShow = true;

            axios.post(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/locations'),
                        this.createLocation,
                        { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(() => {
                    this.setLoadingBar.loadingBarDialogShow = false;
                    this.locationCreateDialogShow = false;
                    this.createLocation = {};
                    this.getLocations();
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.setLoadingBar.loadingBarDialogShow = false;    
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                
            })
        },
        OnLocationModifyOk() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }

            if (!this.modifyLocation.name) {
              this.$alert(common.messages.shippingFrom, "", "info").then(() => {
                this.$refs.shippingFrom.focus();
              });
              return false;
            }
            if (!this.modifyLocation.phoneNumber) {
              this.$alert(common.messages.phoneNo, "", "info").then(() => {
                this.$refs.phoneNumber.focus();
              });
              return false;
            }
            if (!this.modifyLocation.mobileNumber) {
              this.$alert(common.messages.mobileNo, "", "info").then(() => {
                this.$refs.mobileNumber.focus();
              });
              return false;
            }
            if (!this.modifyLocation.address) {
              this.$alert(common.messages.address1, "", "info").then(() => {
                this.$refs.address.focus();
              });
              return false;
            }
            if (!this.modifyLocation.city) {
              this.$alert(common.messages.addressCity, "", "info").then(() => {
                this.$refs.city.focus();
              });
              return false;
            }
            if (!this.modifyLocation.state) {
              this.$alert(common.messages.addressState, "", "info").then(() => {
                this.$refs.state.focus();
              });
              return false;
            }
            if (!this.modifyLocation.zipCode) {
              this.$alert(common.messages.zipCode, "", "info").then(() => {
                this.$refs.postCode.focus();
              });
              return false;
            }

            this.modifyLocation.sellerId = this.$session.get('loginId');
            this.modifyLocation.isDefault = (this.modifyLocation.isDefault == null) ? false : this.modifyLocation.isDefault;
            this.setLoadingBar.loadingBarDialogShow = true;
            
            axios.put(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/locations'),
                        this.modifyLocation,
                        { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(() => {
                    this.setLoadingBar.loadingBarDialogShow = false;
                    this.locationModifyDialogShow = false;
                    this.modifyLocation = {};
                    this.getLocations();
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.setLoadingBar.loadingBarDialogShow = false;    
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                
            })
        },
        OnLocationDelete() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }       

            this.setLoadingBar.loadingBarDialogShow = true;

            axios.delete(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/locations/' + this.modifyLocation.id),
                        { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(() => {
                this.setLoadingBar.loadingBarDialogShow = false;
                this.locationModifyDialogShow = false;

                this.modifyLocation = {};

                this.getLocations();
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.setLoadingBar.loadingBarDialogShow = false;    
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
            })
        },
        getCarriers() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }      

            this.setLoadingBar.loadingBarDialogShow = true;

            axios.get(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/carriers/')
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                this.carriers = response.data;
                this.setLoadingBar.loadingBarDialogShow = false;
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.setLoadingBar.loadingBarDialogShow = false;
            })
        },
        downloadRate(carrier) {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }      

            this.getCarrierRate(carrier);
        },
        getCarrierRate(carrier) {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }      

            let rate = {};

            this.setLoadingBar.loadingBarDialogShow = true;
            axios.get(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/carriers/' + carrier.id + '/rate')
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                rate = response.data;
                this.setLoadingBar.loadingBarDialogShow = false;
                this.getCarrierPeakSurchargeRate(carrier, rate);
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.setLoadingBar.loadingBarDialogShow = false;
            })
        },
        getCarrierPeakSurchargeRate(carrier, rate) {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }      

            let surchargeRates = [];

            this.setLoadingBar.loadingBarDialogShow = true;
            axios.get(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/carriers/' + carrier.id  + '/surcharge-rate?name=PEAK')
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                surchargeRates = response.data;
                let surchargeRate = (surchargeRates && surchargeRates.length) ? surchargeRates[0]: null;
                this.setLoadingBar.loadingBarDialogShow = false;

                this.getCarrierCountry(carrier, rate, surchargeRate);
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.setLoadingBar.loadingBarDialogShow = false;
            })
        },
        getCarrierCountry(carrier, rate, surchargeRate) {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }      

            let counties = {};
            
            this.setLoadingBar.loadingBarDialogShow = true;
            axios.get(common.makeAPIUrl('/carriers/' + carrier.id  + '/countries')
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                counties = response.data;

                if(carrier.name.toUpperCase() === 'UPS') {
                    this.makeUPSRateFile(carrier, rate, surchargeRate, counties);
                } else if(carrier.name.toUpperCase() === 'PX') {
                    this.makePXRateFile(carrier, rate, surchargeRate, counties);
                } else if(carrier.name.toUpperCase() === 'EMS') {
                    this.makeEMSRateFile(carrier, rate, surchargeRate, counties);
                } else if(carrier.name.toUpperCase() === 'GEM') {
                    this.makeGEMRateFile(carrier, rate, surchargeRate, counties);
                } else if(carrier.name.toUpperCase() === 'FEDEX_FICP') {
                    this.makeFICPRateFile(carrier, rate, surchargeRate, counties);
                } else if(carrier.name.toUpperCase() === 'FEDEX_IP') {
                    this.makeIPRateFile(carrier, rate, surchargeRate, counties);
                }

                this.setLoadingBar.loadingBarDialogShow = false;
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.setLoadingBar.loadingBarDialogShow = false;
            })
        },
